import LoginOther from '@/components/login/login-other.vue'
import {  mapMutations } from 'vuex'
import $http from '../../common/api/request.js'
export default {
	data() {
		return {
			//用户输入的内容
			userName: "",
			userPwd: "",
			//验证的规格
			rules: {
				userName: {
					rule: /\S/,
					msg: "账号不能为空"
				},
				userPwd: {
					rule: /^[0-9a-zA-z]{6,16}$/,
					msg: "密码应该为6-16位字符"
				}
			}
		}
	},
	components: {
		LoginOther
	},
	methods: {
		...mapMutations(['login']),
		//关闭当前页面，返回上一页
		goBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		//点击登录
		submit() {
			if (!this.validate('userName')) return;
			if (!this.validate("userPwd")) return;

		
			// 请求登录数据
			$http.request({
				url:"/login",
				method:"POST",
				data:{
					userName:this.userName,
					userPwd:this.userPwd
				}
			}).then((res)=>{
				// 这里有问题
				if(res.success) {
					// 保存用户信息
					this.login(res.data)
					
					uni.showToast({
						title:res.msg,
						icon:"none"
					})
					
					uni.navigateBack({
						delta:1
					})
				}else{
					uni.showToast({
						title:res.msg,
						icon:"none"
					})
				}
				
			}).catch(()=>{
				uni.showToast({
					title:'请求失败',
					icon:'none'
				})
			})





		},
		//判断验证是否符合要求
		validate(key) {
			let bool = true;
			if (!this.rules[key].rule.test(this[key])) {
				uni.showToast({
					title: this.rules[key].msg,
					icon: "none"
				})
				bool = false;
				return false;
			}
			return bool;
		},
		// 监听手机号注册进行跳转
		toTel() {
			uni.navigateTo({
				url: "../login-tel/login-tel"
			})
		}
	}
}
